	module.exports = {"data":{"markdownRemark":{"html":"<p>개회 본회의에서 우리는 교육 시스템의 의사 결정 수준을 포함하여 최근에 상대적으로 우려되어 왔던 안정성 문제에 관해 이야기했습니다. 본토 출신의 일부 급우가 살기가 더 어려워지면 서구의 경제 발전과 해안 지역의 경제 발전의 차이로 인해 문제가 발생한다는 사실을 일반적인 이슈에 제기하는 경향이 없어야한다고 언급했다. 이제 문제가되는데, 어떤 분석은 ​​항상 이런 성격을 지니지 않는 질문을 던졌고, 사회적 의미와 정치적 중요성을 발견했으며, 또한 일종의 \"학습\"이되었습니다. 이러한 학습은 특정 조건 하에서 불필요한 사회적 문제를 야기합니다.</p>\n<p>\"학습\"을하는 사람들은 종종 간단한 문제를 복잡하게하고 복잡한 문제를 단순화하기위한 본능을 가지고 있으며, 이것이 \"학습\"이라고 생각합니다. 한 가지 경우에, 이것은 옳다 : 다른 조건 하에서, 이것은 다시 틀린 것이다.</p>","id":"/home/swain/workspace/political-life/src/pages/ko/0042.md absPath of file >>> MarkdownRemark","fileAbsolutePath":"/home/swain/workspace/political-life/src/pages/ko/0042.md","frontmatter":{"title":"2월 25일 금요일"}}},"pathContext":{"id":"/home/swain/workspace/political-life/src/pages/ko/0042.md absPath of file >>> MarkdownRemark"}}

